export default {
	data() {
		return {
			list:[]
		};
	},
	created(){
		this.$Api.getTopList().then(res=>{
			// console.log(res);
			this.list = res
			
		})
	},
	methods: {
		toDetail(top){
			uni.navigateTo({
				url:"/pages/topDetail/topDetail?id="+top.id,
			})
		}
	},
	components: {

	}
};
